@import url("https://emoji-css.afeld.me/emoji.css");

.Joke {
  display: flex;
  border-bottom: 2px solid #eeeeee;
  align-items: center;
  justify-content: center;
  font-weight: 400;
  padding: 1rem;
}

.Joke-buttons {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 1rem;
  width: 15%;
}

.Joke-text {
  width: 75%;
  font-size: 1.2rem;
}

.Joke-votes {
  width: 50px;
  height: 50px;
  line-height: 50px;
  border-radius: 50%;
  text-align: center;
  font-size: 20px;
  font-weight: 300;
  border: 3px solid red;
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.3);
  /* cursor: pointer; */
}

.fa-arrow-up,
.fa-arrow-down {
  font-size: 1.5rem;
  margin: 10px;
  cursor: pointer;
  transition: all 0.3;
}

.fa-arrow-up:hover {
  animation-name: move-up;
  animation-duration: 0.7s;
  animation-timing-function: ease-out;
  color: #8bc34a;
}

.fa-arrow-down:hover {
  animation-name: move-down;
  animation-duration: 0.7s;
  animation-timing-function: ease-out;
  color: #f44336;
}

@keyframes move-up {
  0%,
  50%,
  100% {
    /* dont move anywhere */
    transform: translateY(0);
  }
  25%,
  75% {
    /* go up */
    transform: translateY(-3px);
  }
}

@keyframes move-down {
  0%,
  50%,
  100% {
    /* dont move anywhere */
    transform: translateY(0);
  }
  25%,
  75% {
    /* go down */
    transform: translateY(3px);
  }
}

.Joke-smiley {
  font-size: 3rem;
  margin-left: auto;
  border-radius: 50%;
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.3);
}

.Joke-smiley:hover {
  animation-name: spin-me;
  animation-duration: 0.7s;
  animation-timing-function: ease-in-out;
  animation-delay: 0s;
}

@keyframes spin-me {
  0% {
    transform: rotate(0deg);
  }
  30% {
    transform: rotate(20deg);
  }
  60% {
    transform: rotate(0deg);
  }
  80% {
    transform: rotate(10deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

 /* Small screens */
@media only screen and (max-width: 1100px) {
  
}

 @media only screen and (max-width: 600px) {
   .Joke-text {
     font-size: 0.8rem;
   }
   .Joke-smiley {
     font-size: 1.7em;
   }
   .fa-arrow-up,
  .fa-arrow-down {
    font-size: 1rem;
  }
  .Joke-votes {
    width: 25px;
    height: 25px;
    font-size: 0.7rem;
    border: 2px solid red;
    line-height: 25px;
  }
  .Joke-buttons {
    width: 25%;
    margin-right: 2px;
  }

 }

@media only screen and (max-width: 450px) {
  .Joke {
    padding: 5px;
  }
  .Joke-text {
    font-size: 0.7rem;
  }
  .Joke-smiley {
    font-size: 1.5em;
  }
  .fa-arrow-up,
 .fa-arrow-down {
   font-size: 0.7rem;
 }
 .Joke-votes {
   width: 20px;
   height: 20px;
   font-size: 0.7rem;
   border: 2px solid red;
   line-height: 20px;
 }
 .Joke-buttons {
   width: 35%;
   margin-right: 2px;
 }
}