@import url(https://emoji-css.afeld.me/emoji.css);
@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Joke {
  display: flex;
  border-bottom: 2px solid #eeeeee;
  align-items: center;
  justify-content: center;
  font-weight: 400;
  padding: 1rem;
}

.Joke-buttons {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 1rem;
  width: 15%;
}

.Joke-text {
  width: 75%;
  font-size: 1.2rem;
}

.Joke-votes {
  width: 50px;
  height: 50px;
  line-height: 50px;
  border-radius: 50%;
  text-align: center;
  font-size: 20px;
  font-weight: 300;
  border: 3px solid red;
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.3);
  /* cursor: pointer; */
}

.fa-arrow-up,
.fa-arrow-down {
  font-size: 1.5rem;
  margin: 10px;
  cursor: pointer;
  transition: all 0.3;
}

.fa-arrow-up:hover {
  -webkit-animation-name: move-up;
          animation-name: move-up;
  -webkit-animation-duration: 0.7s;
          animation-duration: 0.7s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  color: #8bc34a;
}

.fa-arrow-down:hover {
  -webkit-animation-name: move-down;
          animation-name: move-down;
  -webkit-animation-duration: 0.7s;
          animation-duration: 0.7s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  color: #f44336;
}

@-webkit-keyframes move-up {
  0%,
  50%,
  100% {
    /* dont move anywhere */
    transform: translateY(0);
  }
  25%,
  75% {
    /* go up */
    transform: translateY(-3px);
  }
}

@keyframes move-up {
  0%,
  50%,
  100% {
    /* dont move anywhere */
    transform: translateY(0);
  }
  25%,
  75% {
    /* go up */
    transform: translateY(-3px);
  }
}

@-webkit-keyframes move-down {
  0%,
  50%,
  100% {
    /* dont move anywhere */
    transform: translateY(0);
  }
  25%,
  75% {
    /* go down */
    transform: translateY(3px);
  }
}

@keyframes move-down {
  0%,
  50%,
  100% {
    /* dont move anywhere */
    transform: translateY(0);
  }
  25%,
  75% {
    /* go down */
    transform: translateY(3px);
  }
}

.Joke-smiley {
  font-size: 3rem;
  margin-left: auto;
  border-radius: 50%;
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.3);
}

.Joke-smiley:hover {
  -webkit-animation-name: spin-me;
          animation-name: spin-me;
  -webkit-animation-duration: 0.7s;
          animation-duration: 0.7s;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
}

@-webkit-keyframes spin-me {
  0% {
    transform: rotate(0deg);
  }
  30% {
    transform: rotate(20deg);
  }
  60% {
    transform: rotate(0deg);
  }
  80% {
    transform: rotate(10deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

@keyframes spin-me {
  0% {
    transform: rotate(0deg);
  }
  30% {
    transform: rotate(20deg);
  }
  60% {
    transform: rotate(0deg);
  }
  80% {
    transform: rotate(10deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

 /* Small screens */
@media only screen and (max-width: 1100px) {
  
}

 @media only screen and (max-width: 600px) {
   .Joke-text {
     font-size: 0.8rem;
   }
   .Joke-smiley {
     font-size: 1.7em;
   }
   .fa-arrow-up,
  .fa-arrow-down {
    font-size: 1rem;
  }
  .Joke-votes {
    width: 25px;
    height: 25px;
    font-size: 0.7rem;
    border: 2px solid red;
    line-height: 25px;
  }
  .Joke-buttons {
    width: 25%;
    margin-right: 2px;
  }

 }

@media only screen and (max-width: 450px) {
  .Joke {
    padding: 5px;
  }
  .Joke-text {
    font-size: 0.7rem;
  }
  .Joke-smiley {
    font-size: 1.5em;
  }
  .fa-arrow-up,
 .fa-arrow-down {
   font-size: 0.7rem;
 }
 .Joke-votes {
   width: 20px;
   height: 20px;
   font-size: 0.7rem;
   border: 2px solid red;
   line-height: 20px;
 }
 .Joke-buttons {
   width: 35%;
   margin-right: 2px;
 }
}
.JokeList {
  display: flex;
  width: 80%;
  height: 80%;
}

.JokeList-sidebar {
  background: #9575cd;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 30%;
  text-align: center;
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.3), inset 0 0 25px #7e57c2;
  z-index: 2;
}

.JokeList-title {
  font-size: 4rem;
  margin: 2rem;
  color: white;
  font-weight: 300;
  letter-spacing: 0.6rem;
}

.JokeList-title span {
  font-weight: 700;
  letter-spacing: 0;
}

.JokeList-sidebar img {
  width: 50%;
  border-radius: 50%;
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.3);
}

.JokeList-sidebar img:hover {
  -webkit-animation-name: shake;
          animation-name: shake;
  -webkit-animation-duration: 5s;
          animation-duration: 5s;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
}
.JokeList-jokes {
  height: 90%;
  background: white;
  align-self: center;
  width: 70%;
  overflow: scroll;
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.3);
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}
/* Hide scrollbar for Chrome, Safari and Opera */
.JokeList-jokes::-webkit-scrollbar {
  display: none;
}

.spin {
  -webkit-animation-name: spin;
          animation-name: spin;
  -webkit-animation-duration: 5000ms;
          animation-duration: 5000ms;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear; 
}

@-webkit-keyframes spin {
  from {
      transform:rotate(0deg);
  }
  to {
      transform:rotate(360deg);
  }
}

@keyframes spin {
  from {
      transform:rotate(0deg);
  }
  to {
      transform:rotate(360deg);
  }
}

.JokeList-spinner {
  text-align: center;
}

.JokeList-getmore {
  font-size: 1.2rem;
  width: 50%;
  border-radius: 2rem;
  padding: 1rem 2rem;
  color: white;
  font-weight: 700;
  border: none;
  outline: none;
  margin: 2rem;
  word-spacing: 0.7rem;
  cursor: pointer;
  /* transition: 0.8s ease; */
  transition: 0.8s cubic-bezier(0.2, 1, 0.2, 1);
  background: linear-gradient(
    135deg,
    rgba(179, 229, 252, 1) 0%,
    rgba(179, 229, 252, 1) 50%,
    rgba(240, 98, 146, 1) 50%,
    rgba(240, 98, 146, 1) 100%
  );
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.3);
}

.JokeList-getmore:hover {
  transform: translate(-2px);
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.25);
}

@-webkit-keyframes shake {
  0% { transform: translate(2px, 2px) rotate(0deg); }
  5% { transform: translate(1px, 1px) rotate(2deg); }
  10% { transform: translate(-3px, -2px) rotate(-5deg); }
  15% { transform: translate(2px, 2px) rotate(-5deg); }
  20% { transform: translate(-3px, 0px) rotate(1deg); }
  25% { transform: translate(2px, 2px) rotate(-1deg); }
  30% { transform: translate(5px, 2px) rotate(0deg); }
  40% { transform: translate(1px, -1px) rotate(2deg); }
  50% { transform: translate(-1px, 2px) rotate(-1deg); }
  60% { transform: translate(-3px, 1px) rotate(0deg); }
  70% { transform: translate(3px, 1px) rotate(-1deg); }
  80% { transform: translate(-1px, -1px) rotate(1deg); }
  90% { transform: translate(1px, 2px) rotate(0deg); }
  100% { transform: translate(1px, -2px) rotate(-1deg); }
}

@keyframes shake {
  0% { transform: translate(2px, 2px) rotate(0deg); }
  5% { transform: translate(1px, 1px) rotate(2deg); }
  10% { transform: translate(-3px, -2px) rotate(-5deg); }
  15% { transform: translate(2px, 2px) rotate(-5deg); }
  20% { transform: translate(-3px, 0px) rotate(1deg); }
  25% { transform: translate(2px, 2px) rotate(-1deg); }
  30% { transform: translate(5px, 2px) rotate(0deg); }
  40% { transform: translate(1px, -1px) rotate(2deg); }
  50% { transform: translate(-1px, 2px) rotate(-1deg); }
  60% { transform: translate(-3px, 1px) rotate(0deg); }
  70% { transform: translate(3px, 1px) rotate(-1deg); }
  80% { transform: translate(-1px, -1px) rotate(1deg); }
  90% { transform: translate(1px, 2px) rotate(0deg); }
  100% { transform: translate(1px, -2px) rotate(-1deg); }
}

 /* Small screens */
@media only screen and (max-width: 1100px) {
  .JokeList-title {
    font-size: 4rem;
    margin: 1rem;
    letter-spacing: 5px;
  }
  .JokeList-getmore {
    padding: 0.5rem;
    font-size: 1rem;
    word-spacing: 0.3rem;
    width: 50%;
  }
  
}
 @media only screen and (max-width: 600px) {
  .JokeList {
    flex-direction: column;
    align-items: center;
  }
  .JokeList-sidebar {
    flex-direction: row;
    width: 110%;
  }
  .JokeList-sidebar img {
    width: 35%;
  }
  .JokeList-title {
    font-size: 2rem;
    margin: 1rem;
    letter-spacing: 2px;
  }
  .JokeList-jokes {
    width: 100%;
  }
  .JokeList-getmore {
    padding: 0.5rem;
    font-size: 1rem;
    word-spacing: 0.3rem;
    width: 35%;
  }
  
}

@media only screen and (max-width: 450px) {
  .JokeList-title {
    font-size: 1.5rem;
    margin: 1rem;
    letter-spacing: 2px;
  }
  .JokeList-jokes {
    width: 100%;
    margin-top: 5px;
  }
  .JokeList-getmore {
    padding: 0.5rem;
    font-size: 0.8rem;
    word-spacing: 0.3rem;
    width: 30%;
  }
  .JokeList-sidebar img {
    width: 20%;
  }
}
